<template>
    <el-dialog
    width="600px"
    :visible.sync="isVisible"
    :close-on-click-modal="false"
    :close-on-press-escape="false"
    @close="cancel"
    title="请选择质检不通过后的维修方案："
    center
    >
       <el-form ref="qualityForm" :model="qualityForm" :rules="formRules" label-width="30px">

          <el-form-item label="" prop="methods">
              <el-radio-group v-model="qualityForm.methods">
                <el-row v-for="(item,index) in methods"   :key="index">
                  <el-col :span="8">
                    <el-radio :label="item.id" :value="item.id">{{item.name}}</el-radio>
                  </el-col>
                  <el-col :span="16" v-if="item.rechargeable && qualityForm.methods == item.id">
                    <el-form-item label="维修价格" label-width="80px" prop="price">
                      <el-input-number v-model="qualityForm.price" controls-position="right" :min="1" :max="999999" :precision="2" :step="0.01" size="mini"></el-input-number>
                    </el-form-item>
                  </el-col>
                </el-row>
              </el-radio-group>
          </el-form-item>
          <el-form-item label="" prop="goodTestRemark">
            <span>质检不通过的原因:</span>
            <el-input
              v-model="qualityForm.goodTestRemark"
              :rows="5"
              type="textarea"
              maxlength="200"
              placeholder="请输入质检不通过的原因,最长200字符"
              resize="none">
            </el-input>
          </el-form-item>
       </el-form>
       <div slot="footer" class="dialog-footer">
            <el-button size="small" type="primary" @click="save">提交</el-button>
            <el-button size="small" @click="cancel">取消</el-button>
        </div>
    </el-dialog>
</template>
<script>
/**
 * QualityDealFail Component
 * ----------------------
 * Author: Calamus
 * Date: 2018.08.29
 */
// validate
import Validator from '@/utils/extendValidate/extendValidate'
// api
import { maintenanceMethodsList, qualityStatus } from '@/api/warehouse'

export default {
  name: 'QualityDealFail',
  data () {
    return {
      isVisible: false,
      qualityForm: {
        methods: '',
        price: '',
        goodTestRemark: '' // 质检不通过原因，列表备注显示
      },
      formRules: {
        price: [{required: true, message: '请输入价格', trigger: 'change'},
          {validator: Validator.normalNumber, trigger: 'change'}],
        methods: [{required: true, message: '请选择维修方式', trigger: 'change'} ]
      }
    }
  },
  props: {
    show: {
      type: Boolean,
      default: false
    },
    modifyUser: {
      type: Array
    },
    searchParams: {
      type: Object
    },
    methods: {
      type: Array,
      required: true
    },
    ids: {
      type: String,
      required: true
    }
  },
  watch: {
    show (isShow) {
      this.isVisible = isShow
    }
  },
  methods: {
    save () {
      this.$refs.qualityForm.validate(isValid => {
        if (isValid) {
          const searchParams = this.searchParams
          let params = {
            goodTestStatus: 2,
            goodTestResult: this.qualityForm.methods,
            goodTestRemark: this.qualityForm.goodTestRemark,
            goodRepairCost: this.qualityForm.price,
            goodTestItems: []
          }
          for (let item of this.modifyUser) {
            params.goodTestItems.push({'goodTestUuid': item.goodTestUuid, 'productSeq': item.productSeq})
          }
          qualityStatus(params).then(res => {
            if (!res.error) {
              this.$message({
                message: '修改维修方式成功!',
                type: 'success'
              })
              this.cancel()
              this.$emit('updateList', this.searchParams)
            }
          })
        }
      })
    },
    cancel () {
      this.$refs.qualityForm.resetFields()
      this.$emit('update:show', false)
      this.isVisible = false
    }
  }
}
</script>
<style scoped>
.el-form-item--mini.el-form-item, .el-form-item--small.el-form-item {
    margin-bottom: 0px;
}
.el-radio {
    padding: 7.5px;
}
</style>



